/**
 * Star rating
 */
.star-rating {
  overflow: hidden;
  position: relative;
  height: 1.618em;
  line-height: 1.618;
  font-size: 1em;
  width: 5.3em;
  font-family: 'star';
  font-weight: 400; }
  .star-rating:before {
    content: "\53\53\53\53\53";
    opacity: .25;
    float: left;
    top: 0;
    left: 0;
    position: absolute; }
  .star-rating span {
    overflow: hidden;
    float: left;
    top: 0;
    left: 0;
    position: absolute;
    padding-top: 1.5em; }
  .star-rating span:before {
    content: "\53\53\53\53\53";
    top: 0;
    position: absolute;
    left: 0;
    color: #000000; }

p.stars a {
  position: relative;
  height: 1em;
  width: 1em;
  text-indent: -999em;
  display: inline-block;
  text-decoration: none;
  margin-right: 1px;
  font-weight: 400; }
  p.stars a:before {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 1em;
    height: 1em;
    line-height: 1;
    font-family: "star";
    content: "\53";
    color: #000000;
    text-indent: 0;
    opacity: .25; }
  p.stars a:hover ~ a:before {
    content: "\53";
    color: #000000;
    opacity: .25; }

p.stars:hover a:before {
  content: "\53";
  color: #000000;
  opacity: 1; }

p.stars.selected a.active:before {
  content: "\53";
  color: #000000;
  opacity: 1; }

p.stars.selected a.active ~ a:before {
  content: "\53";
  color: #000000;
  opacity: .25; }

p.stars.selected a:not(.active):before {
  content: "\53";
  color: #000000;
  opacity: 1; }

