/**
 * WooCommerce Price Filter
 */
.widget_price_filter {
	.price_slider {
		margin-bottom: 1.5em;
	}

	.price_slider_amount {
		text-align: right;
		line-height: 2.4em;

		.button {
			float: left;
		}
	}

	.ui-slider {
		position: relative;
		text-align: left;
	}

	.ui-slider .ui-slider-handle {
		position: absolute;
		z-index: 2;
		width: 1em;
		height: 1em;
		cursor: ew-resize;
		outline: none;
		background: $color__link;
		box-sizing: border-box;
		margin-top: -.25em;
		opacity: 1;

		&:last-child {
			margin-left: -1em;
		}

		&:hover,
		&.ui-state-active {
			box-shadow: 0 0 0 .25em rgba(#000, 0.1);
		}
	}

	.ui-slider .ui-slider-range {
		position: absolute;
		z-index: 1;
		display: block;
		border: 0;
		background: $color__link;
	}

	.price_slider_wrapper .ui-widget-content {
		background: rgba(0,0,0,0.1);
	}

	.ui-slider-horizontal {
		height: .5em;
	}

	.ui-slider-horizontal .ui-slider-range {
		height: 100%;
	}
}

aside{
	position: sticky;
	align-self: flex-start;
	hr{
		border-color:#000;
		background-color:#000;
	}
	h3{
		span{
			font-size: 1.4rem;
			padding-left: 1rem;
			font-weight: 300;
			text-decoration: underline;
			
			cursor: pointer;
			color: $color_block_palet6_dark;
			transition: color 0.25s;
			&:hover {
				color: $color__link-hover;
			}
		}
	}
	.filters-Tag {
		span{
			margin-right: 0.2rem;
			border: 1px solid #000;
			padding: 0.2rem 0.4rem;
			font-size: 1.2rem;
			border-radius: 0.6rem;
			display: inline-block;
			i {
				&:hover{
					color: $color__link-hover;
					cursor:pointer;
				}
			}
		}
	}
}

.woocommerce-widget-layered-nav {

	h4{
		color: $color_block_palet6_dark;
		font-size: 2rem;
		font-weight: 600;
		border-bottom: 1px solid $color_block_palet6_dark;
		padding-bottom: 1rem;
		cursor: pointer;
		transition: color 0.25s , border-color 0.25s;
		position: relative;
		&:after{
			font-family: "Font Awesome 5 Pro"; 
			font-weight: 300; 
			font-size: 1.5rem;
			top:50%;
			position: absolute;
			transform: translateY(-50%);
			right: 0;
			content: "\f078";
		}
		&:hover{
			color:#000;
			border-color:#000;
		}
		&.open{
			color:#000;
			border-color:#000;
			&:after{
				content: "\f077";
			}
		}
	}


	ul{
		overflow: hidden;
		max-height: 0;
		transition: max-height 0.25s;
		a{
			text-decoration: none;;
		}
		&.open{
				max-height: 200px;
		}
		li.chosen {
			a{
				color: $color__link-hover;
				
			}
		}
	}
	
}

