/**
 * Products
 */
ul.products {
	@include clearfix;
	margin: 0;
	padding: 0;
	display: flex;
	flex-wrap: wrap;
	
	&.columns-3{
		padding-left: 5rem;
		@include media-screen($bk_mobile){
			padding-left: 0;
		}
		li.product{
			padding: 0 2rem;
			width: calc(100% / 3);
			@include media-screen(1000px){
				width: calc(100% / 2);

			}
			@include media-screen($bk_mobile){
				padding: 0 1.5rem;

				width: 100%

			}
		}
	}
	li.product {
		list-style: none;
		position: relative;
		margin-bottom: 2em;
		a{
			text-decoration: none;
			&:hover{
				color:inherit;
				.woocommerce-loop-product__title{
					color: $color__link-hover;
				}
			}
		}
		.woocommerce-loop-product__title {
			padding-top:0;
			margin-top:0;
			border-top: 1px solid #000;
			padding-top:0.6rem;
			font-size: 1.6rem;
			transition: color 0.25s;
			
		}
		figure {
			display: block;
			height: 250px;
			padding: 0 2rem;;
			img {
		
				height: 100%;
				object-fit: contain;
			}
		}
		.enclosure-top-title {
			font-size: 12px;
			text-transform: uppercase;
			font-weight: 600;
		}
		.price-stock{
			display: flex;
			justify-content: space-between;
			font-size: 1.2rem;
			font-weight: 400;
			.stock{
				color : $color_block_palet6_dark;
			}
		}
		.button {
			display: block;
		}
	}
}

@include media-screen(48em) {
	ul.products {
		li.product {
			@include column-width(3);
			float: left;
			margin-right: $columns__margin;

			&.first {
				clear: both;
			}

			&.last {
				margin-right: 0;
			}
		}
	}

	.columns-1 {
		ul.products {
			li.product {
				float: none;
				width: 100%;
			}
		}
	}

